  let page = 1
  export default {
    data () {
      return {
        adList: [],
        newcomerList: [],
        navList: [],
        list: [],
        currIndex: 0,
        currType: 1,
        loadingType: 0,
		  	headerPosition: 'fixed'
      }
    },
    onLoad () {
      this.getData()
    },
    onPullDownRefresh(){
      this.getData()
    },
    methods: {
      linkTo (item) {
        if(!uni.getStorageSync('token')) {
          this.getnav(`/pages/loginSon/login`)

          return false
        }
        uni.setStorageSync('pullurl', item.addressitem)
        uni.setStorageSync('shopid', item.shop_id)
        this.getnav(`/pages/live/Audience`)
      },
      getData () {
        this.$http.getLiveAd().then((res) => {
          if (res.status === 200) {
            this.adList = res.data.data
          }
        })

        this.$http.getaliveindex({
          isnewperson: 1
        }).then(res => {
          if (res.status === 200) {
            this.newcomerList = res.data.data
          } 
        })

        this.$http.getNavList().then(res => {
          if (res.status === 200) {
            this.navList = res.data
          }
        })

        this.$http.getaliveindex({
          typeid: -1,
          page: 1
        }).then(res => {
          if (res.status === 200) {
            this.list = res.data.data
          } 
        })
      },

      changeTab (index, id) {
        if (this.currIndex === index) return false

        this.currIndex = index
        this.currType = id
        this.isMore = false
        page = 1
        this.$http.getaliveindex({
          typeid: id,
          page: 1
        }).then(res => {
          if (res.status === 200) {
            if (res.data.data.length < 10) {
              this.loadingType = 2
            }
            this.list = res.data.data
          } 
        })
      }
    },

    onReachBottom () {
      if (this.loadingType === 2) return false
      this.loadingType = 1
      this.$http.getaliveindex({
        typeid: this.currType,
        page: page + 1
      }).then(res => {
        if (res.status === 200) {
          this.loadingType = 0
          if (!res.data.data.length) {
            this.loadingType = 2
          }
          if (res.data.data.length < 10) {
            this.loadingType = 2
            page = page + 1
          }
          if (res.data.data.length) {
            this.list = [...this.list, res.data.data]
            page = page + 1
          }
        } else {
          this.loadingType = 0
        }
      }).catch(() => {
        this.loadingType = 0
      })
    },
  
    onNavigationBarButtonTap(params) {
      this.getnav('/pages/live/BroadcastLive')
    }
  }
